export const initialState={ // this is where we initalized the things which requires state managment and global access with context api
    basket:[],
    user: null //by default user is null not logged/sign in
};

export const getBasketTotal=(basket)=>
    basket?.reduce((amount,item)=>item.price+amount,0);

const reducer = (state,action)=>{
    console.log(action);
    switch (action.type){
        case"ADD_TO_BASKET":
            return{
                ...state,
                basket:[...state.basket,action.item],
            };
        
        case "REMOVE_FROM_BASKET":
            const index=state.basket.findIndex(
                (basketItem)=>basketItem.id===action.id
            );
            let newBasket=[...state.basket];

            if(index>=0){
                newBasket.splice(index,1);
            }else{
                console.warn(
                    `Cant remove product (id: ${action.id}) as its not in basket!`
                )
            }

            return {
                ...state,
                basket:newBasket
            }
        case "SET_USER":
            return{
                ...state,
                user:action.user
            }

        default:
            return state;
    }
};

export default reducer;